
.input-container {
    display: grid;
    grid-template-columns: 25vw;
    margin: 0px 5px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 3px;
    width: max-content;
    background-color: transparent;
    * {
      margin: 5px;
      color: white;
      background-color: transparent;
    }
    &__label {
      width: 144px;
      height: 19px;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: 300;
      font-size: 1rem;
      color: black;
    }
    &__input {
      width: 339px;
      height: 55px;
      font-weight: bold;
      font-size: 0.8em;
      border: 1px solid black;
      padding-left: 2ch;
      padding-right: 2ch;
      color: black;
    }
  }